.card {
    background: #fff;
    border-radius: 2px;
    border-color: red;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 60%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  }
  .card > .img-container > img {
    width: 60%
  }
  
  .card > .content {
    text-align: center;
    font-family: 'Metal Mania', cursive;
    font-size: 30px;
    color: crimson;
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: bold;
    margin-top: 50px;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  

  
  div{
      display: block;
  }